/**
 * Styles for the video block
 */
.video {
    &__placeholder {
        position: relative;
    }

    &__img {
        display: block;
        max-width: 100%;
    }

    &__overlay {
        bottom: 0;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;

        align-items: center;
        display: flex;
        justify-content: center;

        background: rgba($color-primary, 0.35);
    }

    &__play-btn {
        height: 55px;
        width: 55px;

        background: transparent;
        border: 2px solid $color-white;
        color: $color-white;
        @include font-size(22px);
        line-height: 1;

        cursor: pointer;
        transition: opacity 0.2s linear;

        &:hover {
            opacity: 0.7;
        }

        &:focus {
            box-shadow: 0 0 0 3px rgba($color-white, 0.5);
        }
    }

    &__container {
        position: relative;

        &_format_16x9 {
            padding-bottom: 56.25%;
        }
    
        &_format_4x3 {
            padding-bottom: 75%;
        }
    }

    &__frame {
        left: 0;
        position: absolute;
        top: 0;

        height: 100%;
        width: 100%;
    }
}
